html {
	scroll-behavior: smooth; }

body {
	background-color: $white;
	line-height: 1.86;
	letter-spacing: 0.063em;
	@include verdana;
	font-weight: 400;
	font-size: 15px;
	color: $text;
	min-width: 320px;
	margin: 0;
	padding: 0;
	border: 0;
	@include smooth-font; }

a {
	text-decoration: none;
	color: currentColor;
	&:hover {
		text-decoration: none; } }

